<template>
  <header id="header">
     <!-- 헤더 영역  -->
     <h1 id="top_logo"><a href="/">Xwood</a>
        <router-link to="/"></router-link>
     </h1>
     <nav id="top_nav">
       <ul>
         <li v-for="(list,index) in menuList">
               <router-link :to="list.alink">{{list.text}}</router-link>
               <ul v-if="index == 0" class="submenu">
                 <li v-for="(sublist,index) in submenuCompany">
                   <router-link :to="sublist.alink">{{sublist.text}}</router-link>
                 </li>
               </ul>
               <ul v-if="index == 3" class="submenu">
                 <li v-for="(sublist,index) in submenuCommunity">
                   <router-link :to="sublist.alink">{{sublist.text}}</router-link>
                 </li>
               </ul>
         </li>
       </ul>
     </nav>
  </header>
</template>

<script>
export default {
  name: 'HeaderComponent',
  data () {
    return {
      menuList : [
        { text : 'company' , alink : '/company'},
        { text : 'product' , alink : '/product'},
        { text : 'gallery' , alink : '/gallery'},
        { text : 'community' , alink : '/notice'},
        { text : 'location' , alink : '/location'},
      ],
      submenuCompany : [
        { text : 'introduce' , alink : '/company'},
        { text : 'ci' , alink : '/ci'},
        { text : 'history' , alink : '/history'},
      ],
      submenuCommunity : [
        { text : 'notice' , alink : '/notice'},
        { text : 'faq' , alink : '/faq'},
      ],
    }
  }
}
</script>



